.register-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

}

.register-form {
  width: 400px;

  padding: 30px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

.form-title {
  font-size: 24px;
  font-weight: 600;

  margin-bottom: 30px;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

label {
  font-size: 14px;

  margin-bottom: 5px;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  height: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
  padding: 0 10px;
  background-color: var(--bg-color);
  color: var(--text-color);
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus {
  outline: none;
  border-color: #0f3f66;
}

button[type="submit"] {
  height: 40px;
  background-color: #0f3f66;
  background-color: #0046be;
  color: #fff;
  font-size: 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

button[type="submit"]:hover {
  background-color: #2c5882;
}

.form-group a {
  font-size: 14px;

  text-decoration: none;
  transition: color 0.2s;
}

.form-group a:hover {
  color: #2c5882;
}